.chapterlist_mod {
	padding: 0;
	position: relative;
	overflow: hidden;
	ul{
		overflow: hidden;
	}
	h2 {
		margin: 0;
		padding:20px 0 15px 15px;
		font-size: 16px;
		color: #666;
		font-weight: 700;border-bottom:solid 1px #e6e6e6;
	}
	li {
		position: relative;
		font-size: 14px;padding:15px 60px 15px 0;margin:0 15px;
		border-bottom:solid 1px #e6e6e6;
		a {
			display: block;
			color: #666;
		}
	}
}

