}

.chapter-mod-list {
	overflow: hidden
}

.chapter-mod-list li {
	display: block;
	float: left;
	width: 33.33333333%
}

.chapter-mod-list li a {
	display: block;
	margin: 0 auto;
	width: 90%;
	line-height: 2.6rem;
	text-align: center;
	border-radius: 3px;
