        border-bottom:solid 1px #e5e5e5;color:#999;
}
.chapter-time a{color:#6cd;display:inline-block;padding-left:1rem}

.chapter-buy {
	border-top: solid 1px #ddd;
	font-size: 1.4rem;
	padding-top: 3rem;
	margin: 1rem 1.5rem 0;
	overflow: hidden
}

.chapter-buy-ul {
	width: 100%;
	margin: 2rem 0 1rem;
}

.chapter-buy-ul li {
        position:relative;
	float: right;
	width: 47%;
	text-align: center;
	line-height: 3.2rem;
	border: 1px solid #2be;
	border-radius: 3px
}
.chapter-buy-ul li i{position:absolute;top:-4px;right:3px;border-radius:2px;width:22px;height:22px;font-size:10px;line-height:20px;background:#ff4c2f;color:#fff;}
.chapter-buy-ul li i:after{position:absolute;top:21px;left:0;content: '';width:22px;height:5px;border-left: 11px solid transparent;border-right: 11px solid transparent;border-top: 8px solid #ff4c2f;}

.chapter-buy-ul li.fl {
	float: left;
	background: #2be;
}

.chapter-buy-ul li a {
	color: #2be
}

.chapter-buy-ul li.fl a {
	color: #fff
}

.chapter-buy-p {
	line-height: 1.8rem
}

.chapter-auto {
	position: relative;
	margin-bottom: 3rem;clear:both;padding-bottom:20px;overflow:hidden;
}

.chapter-auto input.checkbox {
	border: none;
	opacity: 0;
	line-height: auto;
	width: 1.6rem;
	height: 1.6rem
}

.chapter-auto span {
	background: url(../../img/mobile/lk_bg.png) 0 -2rem no-repeat;
