.chapter-auto span.on {
	background-position: 0 -4rem
}

.chapter-note {
	color: #999;
	margin-bottom: 3rem;
	line-height: 2rem
}
/*添加微信推广css*/
.chapter_spread{margin:2rem 1.5rem 1rem;font-size:1.4rem;border-radius:5px;overflow:hidden}.chapter_spread ul{overflow:hidden}
.chapter_spread li{line-height:1.8rem;padding-bottom:2rem;}
.chapter_spread li a{display:block;display:-moz-box;display:-webkit-box;display:box;color:#333;}
.chapter_spread li a span{display:block}
.chapter_spread li a span.l{padding-right:1.5rem}
.chapter_spread li a span.l img{width:100px;height:70px;vertical-align: middle;border-radius:5px;}
.chapter_spread li a span.r{-moz-box-flex:1.0;-webkit-box-flex:1.0;box-flex:1.0;height:5.4rem;overflow:hidden;}
.first_div{border-bottom:solid 1px #ddd}
.first_div a{margin:1rem;display:block;position:relative}
.first_div img{display:block;width:100%}
.first_div a span{text-overflow:ellipsis;white-space:nowrap;overflow:hidden;background:#333;position:absolute;bottom:0;left:0;opacity:.7;width:100%;height:34px;line-height:34px;color:#fff;font-size:14px;font-weight:bold;}
.chapter_spread li:last-child{border:0}
.chapter_ma{margin:17px 15px 5px;padding:0 0 10px;border:solid 1px #6cd !important;text-align:center;position:relative;font-size:14px}
.chapter_ma img{width:90%;max-width:260px;}
.chapter_ma p{margin-top:15px}
/*.chapter_ma span{display:block;position:absolute;width:220px;top:-1.3rem;left:50%;margin-left:-110px}*/
.chapter_ma span{display:block;text-align:center;font-size:14px;padding:1rem;background: none !important;}
.chapter-shelf{color:#2be;font-size:1.4rem;text-align:center;}.chapter-shelf a{color:#2be;}
a.addDone{color:#999}
/*chapter menu*/
.chapter-menu-top{position:fixed;top:0;left:0;z-index:999;height:44px;background:#000;opacity:0;width:100%;
	/*overflow:hidden;*/
	-webkit-transition: all .2s;
	transition: all .2s;
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
}
.chapter-menu-top.show{
	opacity: .7;
	-webkit-transform: translateY(0);
	transform: translateY(0);
}
.chapter-menu-top li{float:right;padding-top:6px;}.chapter-menu-top li img{width:30px;}.chapter-menu-top li.l{float:left;padding-left:15px}
.chapter-menu-top li.pd{padding-right:15px}.chapter-menu-top li a{display:block;}.chapter-menu-top li.mg{padding:6px 10px 0;}

.chapter-menu-bottom{position:fixed;bottom:0;left:0;z-index:999;background:#000;opacity:0;width:100%;height:64px;
	-webkit-transition: all .2s;
	transition: all .2s;
	-webkit-transform: translateY(100%);
}
.chapter-menu-bottom.show{
	opacity: .7;
	-webkit-transform: translateY(0);
	transform: translateY(0);
}
.chapter-menu-bottom ul{width:100%;}
.chapter-menu-bottom li{
	position: relative;width:20%;float:left;font-size:12px;padding-top:5px;text-align:center; height: 64px;}
.chapter-menu-bottom li a{display:block;color:#fff}.chapter-menu-bottom li img{vertical-align: middle;width:30px;padding-bottom:5px;}
.chapter-menu-bottom li a span{display:block;}
.chapter-menu-bottom li.chapterFontSize{background:url(../../img/wx/laikan/chapter-menu_font.png) center 5px no-repeat;background-size:30px 30px;padding-top:40px !important;}
.chapter-menu-bottom li.chapterFontSize-on{background:url(../../img/wx/laikan/chapter-menu-font.png) center 5px no-repeat;background-size:30px 30px;color:#6cd;padding-top:40px !important;}
.chapter-menu-bottom li.chapterFontSize-on a{color:#6cd}

.popOver{
    display: none;
    position: absolute;
    z-index: 1;
    width:150px;
    padding:0;
    background: rgba(0, 0, 0, 1);
}
.popOver::before{
    content: '';
    position: absolute;
    /*right:17px;*/
    left:50%;
    margin-left:-12px;
    border: 10px solid #000;
    border-top: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}

.popOver.up{
	width:120px; height: 47px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}
.popOver.up::before{
	left: 50%;
    bottom: -10px;
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}
.popOver.down::before{
    top: -10px;
	margin-left:40px;
}

.popOver ul{width:100%;overflow:hidden;}.popOver ul li{float:left;width:50%;text-align:center;padding:7px 0;}.popOver ul li img{width:30px;}
.chapter-menu-top,.chapter-menu-bottom{display:none}
.chapterAuto{padding:15px 10px 15px 40px;color:#fff;font-size:14px;background:url(../../img/wx/laikan/chapter-menu-autobuy-new.png) 15px 16px no-repeat;background-size:16px 16px;overflow:hidden;}
.chapterAuto.on{background:url(../../img/wx/laikan/chapter-menu-on-autobuy.png) 15px 16px no-repeat;background-size:16px 16px;}

